@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Nunito Sans', sans-serif;
}


.app {
  display: flex;
  height: 100vh;
  justify-content: space-around;
  padding-right: 0;
  background: white;
}

.contentsRight {
  flex-grow: 1;
  padding: .5rem 5rem;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  flex: 0.95;

}

.cardFlex {
  display: flex;
  flex-direction: row;
  justify-content: left;
  flex-wrap: wrap;
  gap: 2rem
}