.cards {
    display: flex;
    justify-content: space-around;
    background: white;
    box-shadow: 0px 1px 2px -1px rgba(0, 0, 0, 0.02);
    border-radius: 5px;
    padding: 2rem 1.5rem;
    margin-bottom: 1rem;
    border: 1px solid gainsboro;
}

.url {
    flex-basis: 23%;
    text-decoration: none;
}

.icons {
    background: rgba(255, 196, 35, 1);
    color: black;
    display: flex;
    flex-direction: row;
    flex: 0.1;
    padding: .5rem 1rem;
    border-radius: 5px;
    align-items: center;
}


.icons svg {
    font-size: 1.5rem;
    margin: 0 auto;
}

.text {
    color: black;
    display: flex;
    flex-direction: column;
    flex: 0.8;
}

.yellow {
    background: rgba(255, 196, 35, 1);
    color: black;
}



@media screen and (max-width: 1050px) {
    .url {
        flex-basis: 23%;
        text-decoration: none;
    }
}

@media screen and (max-width: 700px) {
    .url {
        flex-basis: 31%;
        text-decoration: none;
    }
}

@media screen and (max-width: 550px) {
    .url {
        flex-basis: 98%;
        text-decoration: none;
    }
}